*{
    font-family: sans-serif;
}
.Topbar{
    background-color: #282c34;
    text-align: center;
    position: fixed;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 1;
    
}
.Topbar img{
    width: 100px;
    padding: 15px 0;
}
.container {
    margin-top: 32px;
    display: flex;
    align-items: center;
    padding: 4px 12px;
    width:100%;
    box-sizing: border-box;
}
.right{
    width:40%;
}
.left{
    width: 50%;
    position: relative;
}
.date{
    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50%,-50%);
    font-size: 50px;
    font-weight: 600;
}

.Heart{
    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50%,-50%);
    font-size: 50px;
    font-weight: 600;
}
/* .Heart p{
    animation: beat 1s infinite;
} */
.Heart i{
    color:red;
    animation: beat 1s infinite;
}
@keyframes beat {
    0% {
        transform: scale(1,1);
    }
    50% {
        transform: scale(1.2,1.2);
    }
    100% {
        transform: scale(1,1);
    }
}
.Heart p{
    margin-top: 16px;
}
.left img{
    margin-top: 10px;
    width: 100%;
}
.right h1{
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 48px;
    font-weight: 600;

}
.right p{
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 20px;
}
.colors{
    font-size: 28 px;
    margin-bottom: 8px;
}
.examples{
    width: 100%;
    max-width: 80px;
    border: 2px solid white;
    margin-right:12px;
    border-radius: 8px;
    cursor: pointer;
}
/* .examples:hover{
    border: 2px solid teal;
} */
.Selectedexample{
    border: 2px solid teal;
}
.features{
    min-width: 80px;
    background-color: gainsboro;
    border-radius: 8px;
    border: 0;
    padding: 12px 24px;
    cursor: pointer;
    margin-right: 12px;
    text-align: center;
    box-sizing: content-box;
    font-weight: 600;
}
.Selectedfeature{
    color: white;
    background-color: grey;
}
.buy{
    color: white;
    background-color: teal;
    padding: 12px 24px;
    margin-top: 36px;
    border:0;
    border-radius: 8px;
    font-size: 20px;
    cursor: pointer;
}